<template>
  <div class="container-fluid">

    <div class="row py-3">
      <div class="col-md-12">
        <h1 class="text-center">Client details</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-md-12">
        <client-table :client="client"></client-table>
      </div>

      <div class="col-md-12 text-right">
        <button type="button" class="btn btn-warning" @click="setConfirmAction(resetPass)">Reset Password</button>
        <button type="button" class="btn btn-danger" @click="setConfirmAction(deactivate)">Deactivate</button>
      </div>

      <div class="col-md-12" v-if="hasPortfolio">
        <h2>Portfolio #{{ portfolio.id }}</h2>

        <b-tabs v-model="tabIndex">

          <!-- TAB #1 -->
          <b-tab title="Overview" active>
            <pie-chart v-if="portfolio.assets.length" :assets="portfolio.assets"></pie-chart>
            <div v-if="!portfolio.assets.length" class="alert alert-danger text-center">No Chart Data</div>
            <h3>ROI</h3>
            <roi-table v-if="portfolio.roi" :roi="portfolio.roi"></roi-table>
            <div v-if="!portfolio.roi" class="alert alert-danger text-center">No ROI Data</div>
          </b-tab>

          <!-- TAB #2 -->
          <b-tab title="Transactions" >
            <transactions-table v-if="tabIndex === 1" :portfolioId="portfolio.id"></transactions-table>
          </b-tab>

        </b-tabs>

      </div>

      <loading :loading="loading"></loading>
      <confirm-modal :confimationText="confirmationText" ref="modal" @confirmed="onConfirm"></confirm-modal>
    </div>

  </div>
</template>


<script>
import ClientTable from '@/components/tables/ClientTable'
import ROITable from '@/components/tables/ROITable'
import TransactionsTable from '@/components/tables/TransactionsTable'
import AssetsPieChart from '@/components/charts/AssetsPieChart'
import ConfirmModal from '@/components/modals/ConfirmModal'

export default {
  created: function() {
    const clients = this.$store.getters.getClients
    let client
    let promise

    if (clients && clients.length) {
      client = clients.find(cl => cl.id === this.clientId)
    }

    if (!client) {
      client = this.$store.getters.getClient
    }

    this.loading = true

    if (!client || client.id !== this.clientId) {
      // We have to fetch the client
      promise = this.$store.dispatch('getClient', this.clientId)
    } else {
      // We alrady have the client
      promise = this.$store.dispatch('setClient', client)
    }

    promise
      .then(client => client.portfolios)
      .then(portfolioIds => {
        if (!portfolioIds || !portfolioIds.length) {
          return Promise.reject(new Error('The client does not have any portfolio.'))
        }
        return portfolioIds[0]
      })
      .then(portfolioId => {
        var portfolio = this.$store.getters.getPortfolio
        if (!portfolio || portfolio.id !== portfolioId) {
          return this.$store.dispatch('loadPortfolioDetails', portfolioId)
            .then(() => {
              this.hasPortfolio = true
            })
        }
      })
      .catch(() => {
        this.hasPortfolio = false
      })
      .finally(() => {
        this.loading = false
      })
  },
  components: {
    clientTable: ClientTable,
    roiTable: ROITable,
    transactionsTable: TransactionsTable,
    pieChart: AssetsPieChart,
    confirmModal: ConfirmModal,
  },
  props: ['id'],
  data() {
    return {
      loading: false,
      deactivate: 'deactivate',
      resetPass: 'resetPass',
      confirmAction: null,
      tabIndex: null,
      hasPortfolio: null,
    }
  },
  methods: {
    setConfirmAction(action) {
      this.confirmAction = action
      this.$refs.modal.openModal()
    },
    onConfirm() {
      if (this.confirmAction === this.resetPass) {
        // TODO Request for reseting the password
      } else {
        // TODO Request for deactivate the user
      }
    },
  },
  computed: {
    clientId: function() {
      return +this.id
    },
    client: function() {
      return this.$store.getters.getClient || {}
    },
    portfolio: function() {
      return this.$store.getters.getPortfolio || {}
    },
    confirmationText: function() {
      if (this.confirmAction === this.deactivate) {
        return `Are you sure you want to deactivate user ${this.client.firstName} ${this.client.lastName} with client ID: ${this.client.id}?`
      } else if (this.confirmAction === this.resetPass) {
        return `Are you sure you want to reset the password of ${this.client.firstName} ${this.client.lastName} with client ID: ${this.client.id}? If 'YES', the user will receive an email with a link to set a new password.`
      } else {
        return null
      }
    },
  },
}
</script>



