<template>
  <b-modal hide-footer hide-header size="lg" ref="modal">
    <p class="text-center">{{ confimationText }}</p>

    <div class="d-flex justify-content-between">
      <button @click="onConfirm" class="btn btn-default">Cancel</button>
      <button @click="closeModal()" class="btn btn-danger">Confirm</button>
    </div>

  </b-modal>
</template>


<script>
export default {
  props: {
    confimationText: {},
  },
  data() {
    return {
    }
  },
  methods: {
    openModal() {
      this.$refs.modal.show()
    },
    closeModal() {
      this.$refs.modal.hide()
    },
    onConfirm() {
      this.$emit('confirmed')
      this.closeModal()
    },
  },
}
</script>



