<template>
  <table class="table table-bordered">
    <thead>
      <tr>
        <th>Client ID</th>
        <th>Name</th>
        <th>Email</th>
        <th>Phone</th>
        <th>Contract ID</th>
        <th>Portfolio ID</th>
        <th></th>
      </tr>
    </thead>

    <tbody>
      <tr v-for="(client, index) in clients">
        <td>{{ client.id }}</td>
        <td>
          <router-link :to="'/clients/' + client.id">{{ client.name }}</router-link>
        </td>
        <td>{{ client.email }}</td>
        <td>{{ client.phone }}</td>
        <td>{{ client.contractId }}</td>
        <td>{{ client.portfolios[0] }}</td>
        <td><button type="button" class="btn btn-sm btn-secondary" @click="resetPass(client)">Reset password</button></td>
      </tr>
    </tbody>

  </table>
</template>


<script>
import userService from '@/services/usersService'

export default {
  props: {
    clients: {},
  },
  methods: {
    resetPass(client) {
      if (confirm('Are you sure you want to reset the password of client ' + client.name + '?')) {
        userService.sendMailForNewPass(client.email)
      }
    },
  },
}
</script>



