<template>
  <div class="container-fluid">

    <div class="row py-3">
      <div class="col-md-12">
        <h1 class="text-center">Clients</h1>
      </div>
    </div>

    <div class="row pb-3">
      <div class="col-md-12 d-flex">
        <b-button variant="success" to="/clients/create">Create client</b-button>
      </div>
    </div>

    <div class="row">
      <div class="col-md-12">
        <clients-table :clients="clients"></clients-table>

        <loading :loading="loading"></loading>

      </div>
    </div>

  </div>
</template>


<script>
import ClientsTable from '@/components/tables/ClientsTable'

export default {
  created: function() {
    const clients = this.$store.getters.getClients

    if (!clients || !clients.length) {
      this.loading = true

      this.$store.dispatch('getClients')
        .finally(() => {
          this.loading = false
        })
    }
  },
  components: {
    clientsTable: ClientsTable,
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    clients: function() {
      return this.$store.getters.getClients
    },
  },
}
</script>



