import usersService from '@/services/usersService'
import portfolioService from '@/services/portfolioService'
import router from '@/router'

const state = {
  users: [],
  clients: [],
  client: null,
  csrfToken: null,
  loggedUser: null,
}

const getters = {
  getClient(state) {
    return state.client
  },
  getClients(state) {
    return state.clients
  },
  getUsers(state) {
    return state.users
  },
  getLoggedUser(state) {
    return state.loggedUser
  },
  getToken(state) {
    return state.csrfToken
  },
}

const mutations = {
  createUser(state, payload) {
    state.users.push(payload)
  },
  createClient(state, payload) {
    state.clients.push(payload)
  },
  getUsers(state, payload) {
    state.users = payload
  },
  getClients(state, payload) {
    state.clients = payload
  },
  setClient(state, payload) {
    state.client = payload
  },
  login(state, payload) {
    state.csrfToken = payload.csrfToken
    state.loggedUser = payload.user
  },
  logout(state) {
    state.csrfToken = null
    state.loggedUser = null
  },
}

const actions = {
  login({commit}, credentials) {
    return usersService.login(credentials.email, credentials.password)
      .then(res => {
        commit('login', res)
      })
      .catch(errObj => Promise.reject(errObj))
  },
  logout({commit}, credentials) {
    commit('logout')
    router.push('/login')
    return usersService.logout()
  },
  createClient({ commit }, client) {
    const createUserType = 'createClient'
    let user

    return usersService[createUserType](client)
      .then(resUser => {
        user = resUser
        commit(createUserType, user)
        return portfolioService.createPortfolio(user.id, client.portfolioId)
      })
      .then(resPortdolioID => {
        user.portfolioId = resPortdolioID
        return user
      })
      .catch(errObj => Promise.reject(errObj))
  },
  createUser({ commit }, user) {
    const createUserType = 'createUser'

    return usersService[createUserType](user)
      .then(resUser => {
        commit(createUserType, resUser)
      })
      .catch(errObj => Promise.reject(errObj))
  },
  getUsers({ commit }) {
    return usersService.getUsers()
      .then(users => {
        commit('getUsers', users)
        return users
      })
      .catch(errObj => Promise.reject(errObj))
  },
  getClients({ commit }) {
    return usersService.getClients()
      .then(clients => {
        commit('getClients', clients)
        return clients
      })
      .catch(errObj => Promise.reject(errObj))
  },
  getClient({ commit }, clientId) {
    return usersService.getUser(clientId)
      .then(client => {
        commit('setClient', client)
        return client
      })
      .catch(errObj => Promise.reject(errObj))
  },
  setClient({ commit }, client) {
    commit('setClient', client)
    return Promise.resolve(client)
  },
}

export default {
  state,
  mutations,
  actions,
  getters,
}



