import axios from 'axios'

import {API_V1_URL} from '@/config'

export default {
  uploadCSV(csv) {
    return axios.post(API_V1_URL + '/tx/bulk', csv)
      .then(res => res.data.data)
  },
  getUploadSummary(uploadId) {
    return axios.get(API_V1_URL + '/operation/' + uploadId)
      .then(res => res.data.data)
  },
}



