import Vue from 'vue'
import Vuex from 'vuex'
import portfolio from './modules/portfolio'
import upload from './modules/upload'
import users from './modules/users'

Vue.use(Vuex)

export const store = new Vuex.Store({
  modules: {
    portfolio,
    upload,
    users,
  },
})



