<template>
  <div>
    <div class="alert alert-danger text-center" v-if="error">
      {{ error }}
    </div>

    <table class="table table-striped">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Type</th>
          <th scope="col">Date</th>
          <th scope="col">Input Amount</th>
          <th scope="col">Input Currency</th>
          <th scope="col">Output Amount</th>
          <th scope="col">Output Currency</th>
        </tr>
      </thead>
      <tbody v-if="transactions">
        <tr v-for="transaction in transactions">
          <th>{{ transaction.id }}</th>
          <th>{{ transaction.type }}</th>
          <th>{{ transaction.date }}</th>
          <th>{{ transaction.inAmount || "-" }}</th>
          <th>{{ transaction.inCurrency || "-" }}</th>
          <th>{{ transaction.outAmount || "-" }}</th>
          <th>{{ transaction.outCurrency || "-" }}</th>
        </tr>
      </tbody>
    </table>

    <loading :loading="loading"></loading>

  </div>
</template>

<script>
export default {
  created: function() {
    if (this.portfolioId) {
      this.loadTransaction(this.portfolioId)
    } else {
      this.error = 'Portfolio ID is not prodvided'
    }
  },
  props: {
    portfolioId: {},
  },
  data() {
    return {
      loading: false,
      error: null,
    }
  },
  computed: {
    transactions: function() {
      return this.$store.getters.getTransactions
    },
  },
  methods: {
    loadTransaction(portfolioId) {
      this.error = null
      this.loadfing = true
      this.$store.dispatch('loadTransactions', [portfolioId])
        .catch(errObj => {
          this.error = errObj.response.data.error
        })
        .finally(() => {
          this.loading = false
        })
    },
  },
  watch: {
    portfolioId: function(newId) {
      if (newId) {
        this.loadTransaction(newId)
      }
    },
  },
}
</script>



