<template>
  <div>

    <b-button-group>
      <b-button variant="outline-primary" @click="setValueType('share')">Proportios</b-button>
      <b-button variant="outline-primary" @click="setValueType('pieces')">Pieces</b-button>
      <b-button variant="outline-primary" @click="setValueType('priceBGN')">Value in BGN</b-button>
    </b-button-group>

    <vue-chart
      chart-type="PieChart"
      :columns="columns"
      :rows="rows"
      :options="options">
    </vue-chart>

  </div>
</template>


<script>
export default {
  props: ['assets'],
  data() {
    return {
      valueType: 'share',
      columns: [
        {
          'type': 'string',
          'label': 'Name',
        },
        {
          'type': 'number',
          'label': 'Share',
        },
      ],
      options: {
        width: 900,
        height: 500,
        is3D: true,
      },
    }
  },
  computed: {
    rows: function() {
      return this.assets.map(asset => [asset.name, asset[this.valueType]])
    },
  },
  methods: {
    setValueType: function(type) {
      this.valueType = type
    },
  },
}
</script>



