import uploadService from '@/services/uploadService'

const state = {
  uploadId: null,
  summary: null,
}

const getters = {
  getSummary(state) {
    return state.summary
  },
}

const mutations = {
  setUploadId(state, payload) {
    state.uploadId = payload
  },
  uploadFile(state, payload) {
    state.summary = payload
  },
}

function loadSummary({ commit, state }) {
  const uplId = state.uploadId
  return uploadService.getUploadSummary(uplId)
    .then(resObj => {
      if (resObj.status !== 'pending') {
        // TODO: handle the cases
      }
      commit('uploadFile', resObj.output)
      return resObj
    })
    .catch(errObj => Promise.reject(errObj))
}

const actions = {
  uploadCSV(context, csv) {
    return uploadService.uploadCSV(csv)
      .then(res => {
        const uplId = res.id
        context.commit('setUploadId', uplId)
        return loadSummary(context)
      })
      .catch(errObj => Promise.reject(errObj))
  },
  loadSummary,
}

export default {
  state,
  mutations,
  actions,
  getters,
}



