import portfolioService from '@/services/portfolioService'

const state = {
  transactions: null,
  portfolio: null,
  // portfolioIDs: null,
  totalPortfolios: null,
}

const getters = {
  getTransactions(state) {
    return state.transactions
  },
  // getPorfolioIDs(state) {
  //   return state.portfolioIDs
  // },
  getPortfolio(state) {
    return state.portfolio
  },
  getTotalPortfolios(state) {
    return state.totalPortfolios
  },
}

const mutations = {
  setTransactions(state, payload) {
    state.transactions = payload
  },
  // setPortfolioIds(state, payload) {
  //   state.portfolioIDs = payload
  // },
  setPortfolioDetails(state, payload) {
    state.portfolio = payload
  },
  getTotalPortfolios(state, payload) {
    state.totalPortfolios = payload
  },
  deleteAllTransactions(state) {
    state.transactions = null
  },
}

const actions = {
  loadTransactions({ commit }, options) {
    commit('setTransactions', null)
    return portfolioService.getPortfolioTransactions(...options)
      .then(transactions => {
        commit('setTransactions', transactions)
        return transactions
      })
      .catch(errObj => Promise.reject(errObj))
  },
  loadPortfolioDetails({ commit }, portfolioId) {
    commit('setPortfolioDetails', null)
    return portfolioService.getPortfolioDetails(portfolioId)
      .then(portfolioDetails => {
        commit('setPortfolioDetails', portfolioDetails)
        return portfolioDetails
      })
      .catch(errObj => Promise.reject(errObj))
  },
  // getPortfoliosOfUser({ commit }, userId) {
  //   return portfolioService.getPortfoliosOfUser(userId)
  //     .then(portfolioIDs => {
  //       commit('setPortfolioIds', portfolioIDs)
  //       return portfolioIDs
  //     })
  //     .catch(errObj => Promise.reject(errObj))
  // },
  getTotalPortfolios({ commit }) {
    return portfolioService.getTotalPortfolios()
      .then(totalPortfolios => {
        commit('getTotalPortfolios', totalPortfolios)
        return totalPortfolios
      })
      .catch(errObj => Promise.reject(errObj))
  },
  deleteAllTransactions({ commit }) {
    commit('deleteAllTransactions')
    return portfolioService.deleteAllTransactions()
  },
}

export default {
  state,
  mutations,
  actions,
  getters,
}



