import {API_V1_URL} from '@/config'
import axios from 'axios'

function apiToUser(data) {
  let user = data
  user.name = data.firstName + ' ' + data.lastName
  if (data.role === 'client') {
    user.portfolios = data.portfolios ? data.portfolios.map(portfolio => portfolio.id) : []
  }
  return user
}

function createUser(user) {
  return axios.post(API_V1_URL + '/users', user, {headers: {'Content-Type': 'application/json'}})
    .then(res => apiToUser(res.data.data))
}

function getUsersByRole(role) {
  return axios.get(API_V1_URL + '/users?role=' + role)
    .then(res => {
      return res.data.data.map(user => apiToUser(user))
    })
}

export default {
  getUser(id) {
    return axios.get(API_V1_URL + '/user/' + id)
      .then(res => apiToUser(res.data.data))
  },
  createUser(user) {
    user.role = 'admin'
    return createUser(user)
  },
  createClient(client) {
    client.role = 'client'
    return createUser(client)
  },
  updateUser(id, update) {
    return axios.patch(API_V1_URL + '/user/' + id, update, {headers: {'Content-Type': 'application/json'}})
  },
  getClients() {
    return getUsersByRole('client')
  },
  getUsers() {
    return getUsersByRole('admin')
  },
  // AUTH
  login(email, password) {
    var credentials = {email, password}
    return axios.post(API_V1_URL + '/session', credentials, {headers: {'Content-Type': 'application/json'}})
      .then(res => {
        return {
          user: apiToUser(res.data.data),
          csrfToken: res.data.csrfToken,
        }
      })
      .catch(errObj => Promise.reject(errObj.response.data.error))
  },
  logout() {
    return axios.delete(API_V1_URL + '/session')
  },
  sendMailForNewPass(email) {
    // It will trigger the server to send an email with a link for password reset
    return axios.post(API_V1_URL + '/credentials/lost', {email}, {headers: {'Content-Type': 'application/json'}})
  },
  setNewPass(token, userId, password) {
    // The args are taken from the URL for password reset
    var postData = {token, userId, password}
    return axios.patch(API_V1_URL + '/credentials/reset', postData, {headers: {'Content-Type': 'application/json'}})
  },
  changePass(oldPassword, newPassword) {
    // The user changes the password themselves
    var postData = {oldPassword, newPassword}
    return axios.patch(API_V1_URL + '/credentials/change', postData, {headers: {'Content-Type': 'application/json'}})
  },
}



