<template>
  <div class="container-fluid">

    <div class="row py-3">
      <div class="col-md-12">
        <h1 class="text-center">Fund</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-md-6" v-if="totalPortfolios.assets && totalPortfolios.assets.length">
        <pie-chart :assets="totalPortfolios.assets"></pie-chart>
      </div>

      <!-- <div class="col-md-6" v-if="totalPortfolios.ROI" >
        <h3>ROI</h3>
        <roi-table :roi="totalPortfolios.ROI"></roi-table>
      </div> -->

      <loading :loading="loading"></loading>

    </div>

  </div>
</template>


<script>
import AssetsPieChart from '@/components/charts/AssetsPieChart'
import ROITable from '@/components/tables/ROITable'

export default {
  created: function() {
    this.loading = true
    this.$store.dispatch('getTotalPortfolios')
      .finally(() => {
        this.loading = false
      })
  },
  components: {
    pieChart: AssetsPieChart,
    roiTable: ROITable,
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    totalPortfolios: function() {
      return this.$store.getters.getTotalPortfolios || {}
    },
  },
}
</script>



