<template>
  <div class="container-fluid pt-5">
    <div class="row">
      <div class="col-md-5 mx-auto">
        <upload-file-form @uploadSuccess="showModal = true"></upload-file-form>
      </div>
    </div>

    <b-modal
      v-model="showModal"
      title="Successful Upload"
      header-bg-variant="success"
      header-text-variant="light"
      :cancel-disabled="disableCancelBtn">
      <div slot="modal-header-close"></div>
      <p>The file was uploaded successfully.</p>
      <p v-if="status === 'pending'">The data will be processed shortly.</p>
      <div slot="modal-footer" class="w-100">
        <b-btn size="sm" class="float-right" variant="primary" @click="showModal=false">OK</b-btn>
      </div>
    </b-modal>

  </div>
</template>


<script>
import UploadFileForm from '@/components/forms/UploadFileForm'

export default {
  components: {
    uploadFileForm: UploadFileForm,
  },
  data() {
    return {
      showModal: false,
      disableCancelBtn: false,
    }
  },
  computed: {
    status: function() {
      const summary = this.$store.getters.getSummary
      return (summary && summary.status) ? summary.status : 'pending'
    },
  },
}
</script>



