<template>
  <div>
    <table class="table table-bordered">
      <tr>
        <th>Last 30 days:</th>
        <td>{{ roi.last30days.percentage }}%</td>
        <td>{{ roi.last30days.amountBGN }} BGN</td>
      </tr>
      <tr>
        <th>From the begining of the year:</th>
        <td>{{ roi.fromYearStart.percentage }}%</td>
        <td>{{ roi.fromYearStart.amountBGN }} BGN</td>
      </tr>
      <tr>
        <th>From the begining of the contract:</th>
        <td>{{ roi.fromPortfolioStart.percentage }}%</td>
        <td>{{ roi.fromPortfolioStart.amountBGN }} BGN</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  props: {
    roi: {},
  },
}
</script>



